import React, { useEffect, useRef } from 'react';
import useChat from './hooks/useChatContext';
import styles from './chatMessages.module.css';

function formatSendAt(sendAt: string): string {
  const date = new Date(sendAt);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffMinutes = Math.floor(diffMs / 60000);

  if (diffMinutes < 1) {
    return 'now';
  } else if (diffMinutes === 1) {
    return '1 min ago';
  } else if (diffMinutes < 60) {
    return `${diffMinutes} mins ago`;
  } else if (diffMinutes < 1440) {
    const diffHours = Math.floor(diffMinutes / 60);
    return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`;
  } else {
    const options: Intl.DateTimeFormatOptions = {
      hour: '2-digit',
      minute: '2-digit',
      day: 'numeric',
      month: 'short',
      year: 'numeric',
    };
    return date.toLocaleDateString(undefined, options).replace(',', '');
  }
}

export default function ChatMessages() {
  const { state } = useChat();
  const { messages, followUpQuestions, quickMessages } = state;
  console.log(
    ' ~ ChatMessages ~ followUpQuestions, quickMessages:',
    followUpQuestions,
    quickMessages
  );
  const messageEndRef = useRef<HTMLDivElement>(null);

  // Function to scroll to the bottom
  const scrollToBottom = () => {
    messageEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom(); // Scroll to bottom when messages change
  }, [messages]);

  if (!state.allMessagesLoaded) {
    return (
      <div className={styles.chatContent}>
        <div
          className={styles.waveLoader}
          style={{ justifyContent: 'center', marginTop: 20 }}
        >
          <div className={styles.dot} />
          <div className={styles.dot} />
          <div className={styles.dot} />
        </div>
      </div>
    );
  }

  return (
    <div className={styles.chatContent}>
      {messages.map((message) => (
        <div
          key={message.id}
          className={`${styles.message} ${
            message.sender === 'CUSTOMER'
              ? styles.messageUser
              : styles.messageBot
          }`}
        >
          <div className={styles.messageBubble}>{message.text}</div>
          {message.sendError && message.sender === 'CUSTOMER' ? (
            <button
              className={styles.errorBtn}
              onClick={() => console.log('Resend message')}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="ionicon"
                viewBox="0 0 512 512"
                height="14"
                width="14"
              >
                <path
                  d="M400 148l-21.12-24.57A191.43 191.43 0 00240 64C134 64 48 150 48 256s86 192 192 192a192.09 192.09 0 00181.07-128"
                  fill="none"
                  stroke="currentColor"
                  strokeLinecap="round"
                  strokeMiterlimit="10"
                  strokeWidth="32"
                />
                <path
                  fill="currentColor"
                  stroke="none"
                  d="M464 97.42V208a16 16 0 01-16 16H337.42c-14.26 0-21.4-17.23-11.32-27.31L436.69 86.1C446.77 76 464 83.16 464 97.42z"
                />
              </svg>

              <span className={styles.errorTag}>Failed</span>
            </button>
          ) : (
            <div className={styles.messageTimestamp}>
              {formatSendAt(message.sendAt)}
            </div>
          )}
        </div>
      ))}

      {followUpQuestions &&
        followUpQuestions.length > 0 &&
        followUpQuestions.map((question) => (
          <div className={`${styles.message} ${styles.messageBot}`}>
            <p className={styles.messageBubble}>{question}</p>
          </div>
        ))}

      {/* This div is used as the scroll target */}
      <div ref={messageEndRef} />

      {state.messagesLoading && state.allMessagesLoaded && (
        <div className={styles.waveLoader}>
          <div className={styles.dot} />
          <div className={styles.dot} />
          <div className={styles.dot} />
        </div>
      )}
    </div>
  );
}
