import React, { FC } from 'react';
import styles from './chatBubble.module.css';
import companyLogo from './fallbackLogo.svg';
import useChat from './hooks/useChatContext';

const ChatBubble: FC = () => {
  const { toggleChat, state } = useChat();
  const { isOpen, error, loading, config } = state;

  if (isOpen) return null;

  return (
    <button
      disabled={loading || isOpen || !!error}
      onClick={toggleChat}
      className={styles.chatToggle}
    >
      {loading ? (
        <div
          className={styles.waveLoader}
          style={{ justifyContent: 'center', marginTop: 20 }}
        >
          <div className={styles.dot} />
          <div className={styles.dot} />
          <div className={styles.dot} />
        </div>
      ) : (
        <img
          crossOrigin="anonymous"
          className={styles.chatToggleLogo}
          src={config?.agent?.image}
          alt="Company Logo"
          onError={(e) => {
            e.currentTarget.src = companyLogo;
          }}
        />
      )}
    </button>
  );
};

export default ChatBubble;
