import React, {
  createContext,
  useReducer,
  ReactNode,
  useEffect,
  useCallback,
} from 'react';
import { Message, ChatState } from '../types/chatTypes';
import { chatReducer, initialState } from '../types/chatReducer';
import { fetchConfig, fetchMessages, sendMessageToAgent } from '../lib/chatApi';
import { decryptData, encryptData, uuidv4 } from '../lib/crypto';

// Interface for the ChatContext
export interface ChatContextProps {
  state: ChatState;
  toggleChat: () => void;
  sendMessage: (message: string) => Promise<void>;
}

// Create the context
export const ChatContext = createContext<ChatContextProps | undefined>(
  undefined
);

// Props interface for the ChatProvider
interface ChatProviderProps {
  children: ReactNode;
  accessKey: string;
}

// // ! Encrypt data using AES encryption
// const encryptData = (data: string, accessKey: string) => {
//   return CryptoJS.AES.encrypt(data, accessKey).toString();
// };

// // ! Decrypt data using AES decryption
// const decryptData = (encryptedData: string, accessKey: string) => {
//   const bytes = CryptoJS.AES.decrypt(encryptedData, accessKey);
//   return bytes.toString(CryptoJS.enc.Utf8);
// };

const ChatProvider: React.FC<ChatProviderProps> = ({ children, accessKey }) => {
  const [state, dispatch] = useReducer(chatReducer, initialState);

  // ! Retrieve encrypted tokens from localStorage, decrypting the entire object stored under 'proagentKey'
  const getTokensFromStorage = useCallback(() => {
    const encryptedTokenData = localStorage.getItem('proagentKey');

    if (encryptedTokenData) {
      const decryptedData = decryptData(encryptedTokenData, accessKey);

      try {
        // Parse decrypted data to JSON (contains both tokens)
        const { secreattoken, refreshtoken } = JSON.parse(decryptedData);

        return { secreattoken, refreshtoken };
      } catch (error) {
        console.error('Error parsing decrypted token data:', error);
        return null;
      }
    }
    return null;
  }, [accessKey]);

  // ! Store tokens (secreattoken and refreshtoken) in localStorage as an encrypted JSON object under 'proagentKey'
  const storeTokensInStorage = useCallback(
    (secreattoken: string, refreshtoken: string) => {
      const tokenData = { secreattoken, refreshtoken };

      // Convert token object to JSON and encrypt it
      const encryptedTokenData = encryptData(
        JSON.stringify(tokenData),
        accessKey
      );

      // Store encrypted token data under 'proagentKey'
      localStorage.setItem('proagentKey', encryptedTokenData);
    },
    [accessKey]
  );

  const removeTokensFromStorage = useCallback(() => {
    localStorage.removeItem('proagentKey');
  }, []);

  // Function to toggle chat visibility
  const toggleChat = () => {
    dispatch({ type: 'TOGGLE_CHAT' });
  };

  // Function to send a message and handle API response
  const sendMessage = async (messageText: string) => {
    const message: Message = {
      id: uuidv4(),
      sender: 'CUSTOMER',
      text: messageText,
      sendAt: new Date().toISOString(),
    };

    dispatch({ type: 'SEND_MESSAGE', payload: message });

    try {
      const { data: reply } = await sendMessageToAgent(
        state.config?.agentRoomKey as string,
        messageText
      );
      const responseMessage: Message = {
        id: uuidv4(),
        sender: 'AGENT',
        text: reply.data?.answer || '...',
        sendAt: new Date().toISOString(),
      };

      dispatch({ type: 'RECEIVE_MESSAGE', payload: responseMessage });
      dispatch({
        type: 'SET_FOLLOW_UP_QUESTIONS',
        payload: reply.data?.followUpQuestions || [],
      });
      dispatch({
        type: 'SET_QUICK_MESSAGES',
        payload: reply.data?.quickMessages || [],
      });
      dispatch({ type: 'SET_SOURCES', payload: reply.data?.sources || [] });
    } catch (error) {
      dispatch({
        type: 'SEND_MESSAGE',
        payload: { ...message, sendError: false },
      });
    }
  };

  useEffect(() => {
    const fetchChatConfig = async () => {
      try {
        const storedTokens = getTokensFromStorage();

        let accessToken = '';
        let refreshToken = '';
        if (storedTokens) {
          accessToken = storedTokens.secreattoken; // Use plain token from state
          refreshToken = storedTokens.refreshtoken; // Use plain token from state
        }

        // Call API with access token if available
        const { data: response } = await fetchConfig(accessKey, accessToken);

        if (!accessToken && response.data?.agentRoomKey) {
          accessToken = response.data.agentRoomKey;

          if (response.data?.roomKeyRefreshToken) {
            refreshToken = response.data.roomKeyRefreshToken;
          }
        }

        if (!accessToken || !refreshToken) {
          dispatch({ type: 'CONFIG_ERROR', payload: 'Invalid access key' });
          return;
        }

        if (response.success && response.data) {
          dispatch({
            type: 'CONFIG_SUCCESS',
            payload: {
              agentRoomKey: accessToken,
              roomKeyRefreshToken: refreshToken,
              agent: response.data.agent,
            },
          });

          storeTokensInStorage(accessToken, refreshToken);

          const { data: messagesResponse } = await fetchMessages(accessToken);

          if (messagesResponse.success && messagesResponse.data) {
            dispatch({
              type: 'FETCH_MESSAGES_SUCCESS',
              payload: messagesResponse.data.messages,
            });
          } else {
            dispatch({
              type: 'FETCH_MESSAGES_ERROR',
              payload: messagesResponse.message,
            });
          }
        } else {
          dispatch({ type: 'CONFIG_ERROR', payload: response.message });
        }
      } catch (error) {
        console.log(' ~ fetchChatConfig ~ error:', error);
        removeTokensFromStorage();
        dispatch({ type: 'CONFIG_ERROR', payload: (error as Error).message });
      }
    };

    fetchChatConfig();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [accessKey]);

  return (
    <ChatContext.Provider value={{ state, toggleChat, sendMessage }}>
      {children}
    </ChatContext.Provider>
  );
};

export default ChatProvider;
