// chatReducer.ts

import { ChatState, Action } from './chatTypes';

export const initialState: ChatState = {
  messages: [],
  messagesLoading: false,
  allMessagesLoaded: false,
  followUpQuestions: [],
  quickMessages: [],
  sources: [],
  config: null,
  loading: true,
  error: null,
  isOpen: false,
};

export const chatReducer = (state: ChatState, action: Action): ChatState => {
  switch (action.type) {
    case 'CONFIG_LOADING':
      return { ...state, loading: true, error: null };
    case 'CONFIG_SUCCESS':
      return { ...state, loading: false, config: action.payload };
    case 'CONFIG_ERROR':
      return { ...state, loading: false, error: action.payload };
    case 'SET_TOKENS':
      return {
        ...state,
      };
    case 'CLEAR_TOKENS':
      return { ...state };
    case 'SEND_MESSAGE':
      return {
        ...state,
        messages: [...state.messages, action.payload],
        messagesLoading: true,
      };
    case 'RECEIVE_MESSAGE':
      return {
        ...state,
        messages: [...state.messages, action.payload],
        messagesLoading: false,
      };
    case 'FETCH_MESSAGES_SUCCESS':
      return { ...state, messages: action.payload, allMessagesLoaded: true };
    case 'FETCH_MESSAGES_ERROR':
      return { ...state, error: action.payload };
    case 'TOGGLE_CHAT':
      return { ...state, isOpen: !state.isOpen };
    case 'SET_FOLLOW_UP_QUESTIONS':
      return { ...state, followUpQuestions: action.payload };
    case 'SET_QUICK_MESSAGES':
      return { ...state, quickMessages: action.payload };
    case 'SET_SOURCES':
      return { ...state, sources: action.payload };
    default:
      return state;
  }
};
