import React from 'react';
import { createRoot } from 'react-dom/client';
import ProAgent from './app';

// Export the component
export { ProAgent };

// Add a render function for easy use in script tags
export function render(accessKey: string, elementId?: string) {
  const element = document.getElementById(elementId || 'proagent');

  if (element) {
    const root = createRoot(element);
    root.render(React.createElement(ProAgent, { accessKey }));
  } else {
    console.error(`Element with id "${elementId}" not found`);
  }
}

// Expose the render function globally
(window as any).ProAgent = { render };
