import React from 'react';
import useChat from './hooks/useChatContext';
import styles from './chatHeader.module.css';

import fallbackLogo from './fallbackLogo.svg';

// add proxy to the image url is not https
const getProxyUrl = (url?: string) => {
  const proxyUrl = 'https://images.weserv.nl';
  if (url?.startsWith('http')) {
    return `${proxyUrl}/?url=${url}`;
  }
  return url;
};

export default function ChatHeader() {
  const { state, toggleChat } = useChat();
  const { config } = state;

  return (
    <div className={styles.chatHeader}>
      <div className={styles.chatLogo}>
        <img
          crossOrigin="anonymous"
          src={getProxyUrl(config?.agent?.image)}
          alt={config?.agent?.name}
          className={styles.botLogo}
          onError={(e) => {
            e.currentTarget.src = fallbackLogo;
          }}
        />
      </div>

      <div className={styles.chatInfo}>
        <span className={styles.onlineStatus}>
          <span className={styles.statusDot} />
        </span>
        <h3>{config?.agent?.name}</h3>
      </div>

      <div className={styles.closeBtn}>
        <button onClick={toggleChat} className={styles.close}>
          {/* svg close */}
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="22"
            height="22"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
            className="feather feather-x"
          >
            <line x1="18" y1="6" x2="6" y2="18"></line>
            <line x1="6" y1="6" x2="18" y2="18"></line>
          </svg>
        </button>
      </div>
    </div>
  );
}
