// chatApi.ts

import axios from 'axios';
import { ChatConfig, IReply, Message, ResponseType } from '../types/chatTypes';

const AxiosClient = axios.create({
  baseURL: 'https://qnfemk6n6i.eu-west-1.awsapprunner.com',
  headers: {
    'Content-Type': 'application/json',
  },
});

export const fetchConfig = async (accessKey: string, accessToken?: string) => {
  return AxiosClient.get<ResponseType<ChatConfig>>(
    '/api/v1/agent/config-room',
    {
      headers: {
        'X-Access-Key': accessKey,
        'X-Agentroom-Key': accessToken,
      },
    }
  );
};

export const fetchMessages = async (agentRoomKey: string) => {
  return AxiosClient.get<ResponseType<{ messages: Message[] }>>(
    '/api/v1/agent/messages',
    {
      headers: {
        'X-Agentroom-Key': agentRoomKey,
      },
    }
  );
};

export const sendMessageToAgent = async (
  agentRoomKey: string,
  query: string
) => {
  return AxiosClient.post<ResponseType<IReply>>(
    '/api/v1/agent/ask',
    { query },
    {
      headers: {
        'X-Agentroom-Key': agentRoomKey,
      },
    }
  );
};
