// Simple XOR-based encryption and decryption
export const encryptData = (data: string, accessKey: string) => {
  let result = '';
  for (let i = 0; i < data.length; i++) {
    result += String.fromCharCode(
      data.charCodeAt(i) ^ accessKey.charCodeAt(i % accessKey.length)
    );
  }
  return btoa(result); // Encoding in base64 to handle non-printable characters
};

export const decryptData = (encryptedData: string, accessKey: string) => {
  const decodedData = atob(encryptedData);
  let result = '';
  for (let i = 0; i < decodedData.length; i++) {
    result += String.fromCharCode(
      decodedData.charCodeAt(i) ^ accessKey.charCodeAt(i % accessKey.length)
    );
  }
  return result;
};

// UUID generation without any libraries
export const uuidv4 = () => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0;
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};
